import React, { useCallback, useState } from 'react';

const App = (props: {message: string}) => {
    const [count, setCount] = useState(0);

    const increment = useCallback(() => {
        setCount((count) => count +1);   
        }, [count]);
    



    return(
        <>
        <h1>{props.message}</h1>
        <h2>Count: {count}</h2>
        <button onClick={increment}>Icrement</button>        
        </>
    );
};
export default App;